@use 'base/variables' as v;
@use 'base/mixins' as m;

.paginacion{
    margin-top: 5rem;
    margin-bottom: 3rem;
    display: flex;
    gap: 1rem;
    flex-wrap: wrap;
    justify-content: center;
    align-items: center;
    @include m.tablet { 
        justify-content: flex-end;
     }
    &__enlace{
        color: v.$marronSuave;
        font-size: 1.6rem;
        &--texto{
            padding: 1rem;
            border-radius: .5rem;
            border: 1px solid v.$marronSuave;

            &:hover{
                background-color: v.$marronSuave;
                color: v.$blanco;
            }

        }
        &--actual{
            color: v.$gris;
            font-weight: v.$bold;
            display: none;
            @include m.tablet { 
              display: inline;
             }
        }

        &--numero{
            font-weight: v.$bold;
            display: none;
            @include m.tablet { 
              display: inline;
             }
        }
    }
}